export function title(obj) {
    return `
        <div class="row document-block">
            <div class="col-sm">
                <h1>${obj.value}</h1>
            </div>
        </div>
        `
}

export function text(obj) {
    return `
    <div class="row document-block">
        <div class="col-sm">
            <p>${obj.value}</p>
        </div>
    </div>
    `
}

export function columns(obj) {
    let html = ''

    obj.value.forEach(item => {
        html += `
        <div class="col-sm columns-item ">
            <p>${item}</p>
        </div>
        `
    })

    return `<div class="row document-block columns">${html}</div>`
}

export function image(obj) {
    return `
        <div class="row">
            <img src="${obj.value}" alt="">
        </div>
    `
}

export function button(obj, onClick = undefined) {
    return `
        <div class="button" aria-labelledby="button" aria-roledescription="button">
            <span class="button-text">${obj.value}</span>
        </div>
    `
}